.Navbar_container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    position: fixed;
    width: 100%;
    /* height: 6rem; */
    z-index: 9;
    /* background-color: rgb(249, 202, 114); */
}
.Navbar_container-bg{
    background-color: rgba(0, 0, 0, 0.8);
    transition: 0.3s;
}
.Navbar_container a h1{
    font-size: 4rem;
    font-family: 'Dancing Script', cursive;
    font-weight: 700;
}
.nav_menu{
    display: flex;
}
.nav_menu li{
    padding: 0 1rem;
}
/* .nav_menu li:hover{
    background: red;
} */
.nav_menu li a{
    font-size: 1.5rem;
    font-weight: 500;
}
.hamburger{
    display: none;
}

/* --------------------responsive 900px---------------- */

@media (max-width:900px) {
    .nav_menu{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        background: rgba(0, 0, 0, 0.9);
        position: absolute;
        top: 0;
        left: -100%;
        z-index: -3;
        transition: all 0.4s;
    }
    .nav_menu.active{
        left: 0;
    }
    .Navbar_container a h1{
        font-size: 3rem;
    }
    .nav_menu li{
        padding: 1rem 0;
    }
    
    .nav_menu li a{
        font-size: 2rem;
    }
    .hamburger{
        display: initial;
    }
}